@use 'base/mixins' as m
@use 'base/variables' as v

.lineup
    background-color: v.$pink
    padding: 2rem

    h3, p
        text-align: center
        color: v.$white

    .date-festival
        text-transform: uppercase
        margin: 5rem 0

.concerts-container
    @include m.tablet()
        @include m.grid(2, 5rem)

    p
        color: v.$black

.concert
    margin-bottom: 2rem
    padding-top: 2rem
    border-radius: 2rem
    @include m.tablet() 
        margin-bottom: 0
    .concert-name
        &:after
            content: ""
            display: block
            width: 20rem
            height: 10rem
            background-size: 15rem
            background-repeat: no-repeat
            background-position: center center
            margin: 2rem auto 0 auto

.edm .concert-name:after
    background-image: url(../img/headphone_icon.png)
.rock .concert-name:after
    background-image: url(../img/rock_icon.png)

.schedule
    @include m.reset-list()
    li
        padding: 2.5rem
        font-weight: 700
        span
            font-weight: 400

.bg-yellow
    background-color: v.$yellow
    li:nth-child(odd)
        background-color: darken(v.$yellow , 10%)
.bg-green
    background-color: v.$green
    li:nth-child(odd)
        background-color: darken(v.$green , 5%)
