@use "base/variables" as v
@use "base/mixins" as m

.header
    background-color: v.$green

    @include m.tablet() 
        &.fixed
            left: 0
            top: 0
            position: fixed
            width: 100%
            box-shadow: .1rem .2rem .1rem #333

    h1
        color: v.$white
        text-align: center
        margin-bottom: 4rem
        @include m.tablet()
            margin-bottom: 0

    .content-header
        padding: 2rem
        @include m.tablet()
            display: flex
            justify-content: space-between
            align-items: center

.nav
    @include m.tablet()
        display: flex
        gap: 2rem

    a
        display: block
        text-align: center
        color: v.$white
        font-size: 2.4rem
        margin-bottom: 1rem
        @include m.tablet()
            margin-bottom: 0

        &:hover
            color: v.$yellow
