@use 'base/mixins' as m
@use 'base/variables' as v


.tickets
    padding: 5rem 0 8rem 
    h3
        text-align: center
        color: v.$white 
        margin-bottom: 2rem

    .container-tickets
        @include m.tablet() 
            @include m.grid(2, 8rem)

    
.ticket

    &-header, &-footer 
        padding: .8rem
        p
            text-align: center
            color: v.$white
            margin: 0

    &-header
        background-color: v.$pink
        border-start-start-radius: 1rem
        border-top-right-radius: 1rem
        p
            text-transform: uppercase
            font-weight: 700
            font-size: 1.8rem
    &-body
        background-color: v.$white
        ul
            @include m.reset-list()
            li,:nth-child(odd)
                background-color: v.$white
                padding: 1rem
                border-bottom: 1px solid #e1e1e1
    &-footer
        background-color: v.$purple
        border-end-end-radius: 1rem
        border-bottom-left-radius: 1rem
        p
            font-size: 2.4rem
            font-weight: bold

    &-name
        text-align: center
        font-size: 2.7rem
        color: v.$white
