@use "base/variables" as v
@use "base/mixins" as m

.video
    position: relative
    max-height: 80rem
    min-height: 40rem
    overflow: hidden
    .overlay
        position: absolute
        background: rgb(131,58,180)
        background: linear-gradient(90deg, rgba(131,58,180,0.5) 0%, rgba(253,29,29,0.5) 50%, rgba(252,176,69,0.5) 100%)
        width: 100%
        height: 100%
        display: flex
        flex-direction: column
        justify-content: center
    video
        margin: -20rem 0 0 -60rem
        width: auto
        display: block
        @include m.tablet() 
            width: 100%
            margin: 0 auto

.content-video
    z-index: 1
    h2
        color: v.$white
        font-size: 7rem
        margin: 0
        @include m.desktop()
            font-size: 7rem

    p
        background-color: v.$pink
        display: inline-block
        padding: 1rem
        color: v.$white
        font-weight: 700
        font-size: 2.4rem
        border-radius: .7rem
        margin: 0
        @include m.tablet()
            font-size: 2.4rem
