html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fixed-body {
  overflow: hidden;
}

@media (min-width: 768px) {
  .body-scroll {
    padding-top: 8.6rem;
  }
}
p {
  font-size: 2rem;
  color: #000000;
  line-height: 1.5;
}

.container {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 4rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.header {
  background-color: #4cb8b3;
}
@media (min-width: 768px) {
  .header.fixed {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    box-shadow: 0.1rem 0.2rem 0.1rem #333;
  }
}
.header h1 {
  color: #ffffff;
  text-align: center;
  margin-bottom: 4rem;
}
@media (min-width: 768px) {
  .header h1 {
    margin-bottom: 0;
  }
}
.header .content-header {
  padding: 2rem;
}
@media (min-width: 768px) {
  .header .content-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@media (min-width: 768px) {
  .nav {
    display: flex;
    gap: 2rem;
  }
}
.nav a {
  display: block;
  text-align: center;
  color: #ffffff;
  font-size: 2.4rem;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .nav a {
    margin-bottom: 0;
  }
}
.nav a:hover {
  color: #fef460;
}

.video {
  position: relative;
  max-height: 80rem;
  min-height: 40rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  background: rgb(131, 58, 180);
  background: linear-gradient(90deg, rgba(131, 58, 180, 0.5) 0%, rgba(253, 29, 29, 0.5) 50%, rgba(252, 176, 69, 0.5) 100%);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  margin: -20rem 0 0 -60rem;
  width: auto;
  display: block;
}
@media (min-width: 768px) {
  .video video {
    width: 100%;
    margin: 0 auto;
  }
}

.content-video {
  z-index: 1;
}
.content-video h2 {
  color: #ffffff;
  font-size: 7rem;
  margin: 0;
}
@media (min-width: 992px) {
  .content-video h2 {
    font-size: 7rem;
  }
}
.content-video p {
  background-color: #f53756;
  display: inline-block;
  padding: 1rem;
  color: #ffffff;
  font-weight: 700;
  font-size: 2.4rem;
  border-radius: 0.7rem;
  margin: 0;
}
@media (min-width: 768px) {
  .content-video p {
    font-size: 2.4rem;
  }
}

.info-festival {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .info-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.content-festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .content-festival {
    margin-top: 0;
  }
}
.content-festival h2 {
  margin-bottom: 2rem;
}
.content-festival .date-festival {
  color: #f53756;
  font-weight: bold;
}

.lineup {
  background-color: #f53756;
  padding: 2rem;
}
.lineup h3, .lineup p {
  text-align: center;
  color: #ffffff;
}
.lineup .date-festival {
  text-transform: uppercase;
  margin: 5rem 0;
}

@media (min-width: 768px) {
  .concerts-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.concerts-container p {
  color: #000000;
}

.concert {
  margin-bottom: 2rem;
  padding-top: 2rem;
  border-radius: 2rem;
}
@media (min-width: 768px) {
  .concert {
    margin-bottom: 0;
  }
}
.concert .concert-name:after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0 auto;
}

.edm .concert-name:after {
  background-image: url(../img/headphone_icon.png);
}

.rock .concert-name:after {
  background-image: url(../img/rock_icon.png);
}

.schedule {
  margin: 0;
  padding: 0;
  list-style: none;
}
.schedule li {
  padding: 2.5rem;
  font-weight: 700;
}
.schedule li span {
  font-weight: 400;
}

.bg-yellow {
  background-color: #fef460;
}
.bg-yellow li:nth-child(odd) {
  background-color: #fef02d;
}

.bg-green {
  background-color: #4cb8b3;
}
.bg-green li:nth-child(odd) {
  background-color: #43a8a3;
}

.gallery {
  padding: 5rem 0;
}
.gallery h3 {
  text-align: center;
}

.gallery-images {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .gallery-images {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .gallery-images {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .gallery-images {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

.overlay {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay picture {
  display: block;
  max-width: 80rem;
}
.overlay .btn-close {
  cursor: pointer;
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-color: #ffffff;
  color: #000000;
  display: grid;
  place-items: center;
  font-weight: 900;
}

.tickets {
  padding: 5rem 0 8rem;
}
.tickets h3 {
  text-align: center;
  color: #ffffff;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .tickets .container-tickets {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 8rem;
  }
}

.ticket-header, .ticket-footer {
  padding: 0.8rem;
}
.ticket-header p, .ticket-footer p {
  text-align: center;
  color: #ffffff;
  margin: 0;
}
.ticket-header {
  background-color: #f53756;
  border-start-start-radius: 1rem;
  border-top-right-radius: 1rem;
}
.ticket-header p {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 1.8rem;
}
.ticket-body {
  background-color: #ffffff;
}
.ticket-body ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.ticket-body ul li, .ticket-body ul :nth-child(odd) {
  background-color: #ffffff;
  padding: 1rem;
  border-bottom: 1px solid #e1e1e1;
}
.ticket-footer {
  background-color: #752f97;
  border-end-end-radius: 1rem;
  border-bottom-left-radius: 1rem;
}
.ticket-footer p {
  font-size: 2.4rem;
  font-weight: bold;
}
.ticket-name {
  text-align: center;
  font-size: 2.7rem;
  color: #ffffff;
}

.footer {
  padding: 1rem;
  background-color: #f53756;
}
.footer p {
  margin: 0;
  text-align: center;
  color: #ffffff;
}